.imageTable {
  width: 90%;
  height: 650px;
  margin: 10% auto;
}
body {
  overflow: hidden; /* Hide scrollbars */
}
.scene {
  width: 70%;
  margin: 0 0 0 300px;
  height: 100vh;
}

.popup {
  width: 400px;
  height: 400px;
  margin: 10% auto;
  padding: 10px;
  position: fixed;
  top: 10%;
  left: 40%;
  background-color: #3D75D4;
  border-radius: 10px;
  // color: white;
  box-shadow: rgba(29, 29, 29, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.timer{
  width: 300px;
  height: 100px;
  background-color: #3D75D4;
  position: absolute;
  top: 5%;
  color: white;
  right: 8%;
  border: 1px solid;
  border-radius: 5px;
}

.RC2{
  position: absolute;
  top: 15%;
  right: 30%;
}

.RC1{
  position: absolute;
  top: 15%;
  left: 30%;
}