.role {
  width: 300px;
  height: 200px;
  margin: 10% auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(29, 29, 29, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.Button {
  width: 40%;
  height: 30px;
  background-color: #0B1E40;
  border-radius: 20px;
  color: white;
  margin: 20% auto;
}


.role-btn {
  width: 180px;
  height: 60px;
  font-size: 16px;
  border: none;
  border-radius: 6px;
  background-color: black;
  color: white;
  cursor: pointer
}
.Intro{
    width: 50%;
    border: 2px solid;
    margin: 5% auto;
    border-radius: 10px;
    background-color: lightblue;
}
.InformationData{
  width: 60%;
  margin: -5% auto;
  p{
    text-align: left;
  }
}
.ul{
  text-align: left;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  border: 1px solid black;
  width: 100%;
  caption{
    width: 100%;
    border: 1px solid;
    height: 30px;
    font-weight: bold;
    font-size: large;
  }
}
tr:nth-child(11) {
  background-color: #dddddd;
  font-weight: bold;

}
td, th {
  border: 1px solid black;
  text-align: left;
  padding: 8px;
}

