@import "../../styles/colors";

#notebook {
  z-index: 999;
  position: absolute;
  top: 0;
  left: 0px;
  width: 320px;
  height: 90vh;
  background: #043461;
  border-radius: 10px;
  //transition: all .3s;

  &.expanded {
    right: 0;
  }

  &.open:before {
    content: "";
    z-index: -1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }

  #notebook-head {
    padding: 28px 30px 28px 67px;
    // background: $navy1;
    // background: $navy2;
    background-color: black;
    img{
      width: 100px;
      height: 100px;
      margin-left: -70px;
      // border-radius: 10px ;
    }

    .notebook-icon {
      cursor: pointer;
      position: absolute;
      top: -2px;
      right: -1px;
      width: 96px;
      height: 100px;
      filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
      -webkit-filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
      cursor: pointer;
    }

    #notebook-title {
      margin: 0;
      color: black;
      line-height: 34px;
      text-transform: uppercase;
      letter-spacing: 1px;
      font-size: 67px;
      font-weight: "NotoSans", sans-serif;
    }
    #notebook-sub-title {
      margin: 0;
      color: white;
      line-height: 14px;
      font-weight: lighter;
      letter-spacing: 1px;
      margin-top: 5px;
    }
  }

  .notebook-body {
    height: calc(100% - 91px);
    padding: 2px 0 0 0;
    background: white;
    box-shadow: -8px 0 8px 0 rgba(0, 0, 0, 0.25);
  }
}
.Protocol {
  width: 300px;
  height: 500px;
  background-color: white;
  margin: 10px auto;
  padding: 5px;
  .pText{
    text-align: left;
  }
  .pIcon{
    text-align: right;
    margin-top: -17px;
  }
  .Psteps{
    width: 96%;
    margin-top: 10px;
    padding: 5px;
    height: 30px;
    border: 1px solid;
    // background-color: #3D75D4;
  }
}
.ProtocolButton {
  width: 80%;
  height: 30px;
  background-color: white;
  border-radius: 20px;
}

.notebook-icon-button {
  cursor: pointer;
  position: absolute;
  top: -2px;
  right: -50px;
  width: 96px;
  height: 100px;
  // border: none;
  border-radius: 10px;
  // background: none  ;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  -webkit-filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  cursor: pointer;
}
